<template>
	<div>
		<search :game="game"></search>
		<list :game="game" :hot="hot"></list>			
	</div>
</template>
<script type="text/javascript">
	import search from "./search"
	import list from "./list"
	import axios from "axios"
	export default{
		data (){
			return {
				game:{},
				hot:[]
			}
		},
		components:{
			search,
			list
		},
		mounted:function() {
			axios.get("/api/game.json").then((res)=>{
				var res=res.data;
				if(res.flag&&res.data){
					var data=res.data;
					this.game=data.games
					this.hot=data.hot
					console.log(this.hot)
				}
			})
		}
	}
</script>
<style type="text/css" scoped>

</style>


